.calculator {
    /* styles for the calculator container */
    width: 300px;
    margin: 0 auto;
    /* ... */
  }
  
  .display {
    /* styles for the display screen */
    height: 60px;
    font-size: 24px;
    /* ... */
  }
  
  .buttons {
    /* styles for the buttons container */
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
    /* ... */
  }
  
  .buttons button {
    /* styles for the buttons */
    height: 60px;
    font-size: 24px;
    /* ... */
  }
  
  /* media query for smaller screens */
  @media (max-width: 768px) {
    .calculator {
      /* styles for calculator container on smaller screens */
      width: 100%;
    }
  
    .buttons {
      /* styles for buttons container on smaller screens */
      grid-template-columns: repeat(3, 1fr);
      gap: 5px;
    }
  
    .buttons button {
      /* styles for buttons on smaller screens */
      height: 40px;
      font-size: 18px;
    }
  }